}

.photo-card:hover {
  object-fit: cover;
  transform: scale(1.03);
}

.gallery__image {
  width: 100%;
  height: 250px;
  object-fit: cover;
  display: block;
}

.info {
  justify-content: space-around;
  padding: 10px;
